/* Общие стили */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background: linear-gradient(to right, #ff7e5f, #feb47b);
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Задаем минимальную высоту для тела страницы */
}

/* Стили для header */
header {
  background-image: url('components/background.jpg'); /* Укажите путь к вашему изображению */
  background-size: cover; /* Чтобы изображение растягивалось на весь фон */
  background-position: center center; /* Центрирует изображение */
  background-repeat: no-repeat; /* Запрещаем повторение изображения */
  color: white; /* Белый текст, чтобы контент был видим на темном фоне */
  padding: 20px 0;
  width: 100%;
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* Логотип и текст слева */
  max-width: 1200px;
  margin: 0 auto;
  margin-right: 20px;
}

.logo {
  width: 250px; /* Ширина логотипа */
  height: 250px; /* Высота логотипа */
  display: flex;
  justify-content: left;
  align-items: left;
  border-radius: 50%; /* Округлая форма */
  overflow: hidden; /* Скрытие лишней части */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Тень */
  transition: all 0.3s ease-in-out;
}

.logo img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.header-text {
  text-align: center;
  font-family: "Monotype Corsiva";
}

.header-text h1 {
  font-size: 2rem;
  margin: 20px;
  font-family: "Monotype Corsiva";
  color: whitesmoke;
}

.header-text h3 {
  font-size: 1.5rem;
  margin: 0;
  color: #8913dd;
}

/* Стили для боковой навигации */
.nav {
  width: 220px;
  background-color: #222;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav h3 {
  font-family: "Monotype Corsiva"; /* Указываем шрифт */
  font-size: 1.5rem; /* Размер шрифта */
  color: #333; /* Цвет текста */
  background-color: #f0f0f0; /* Светлый фон, чтобы выделить заголовок */
  padding: 10px 20px; /* Отступы вокруг текста */
  border: 2px solid #333; /* Рамка вокруг заголовка */
  border-radius: 5px; /* Закругленные углы рамки */
  text-align: center; /* Выравнивание текста по центру */
  font-weight: bold; /* Сделать текст жирным */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Тень для текста */
}

.nav h3:hover {
  background-color: #e0e0e0; /* Меняем цвет фона при наведении */
  border-color: #007BFF; /* Меняем цвет рамки */
  cursor: pointer; /* Изменяем курсор на pointer, чтобы указать на интерактивность */
}

.nav ul {
  list-style: none;
  padding: 0;
  width: 100%;
}

.nav ul li {
  width: 100%;
}

.nav ul li a {
  display: block;
  text-align: center;
  padding: 15px;
  background-color: #444;
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  border-bottom: 1px solid #333;
  transition: background-color 0.3s;
}

.nav ul li a:hover {
  background-color: #888;
}

.nav .company-name {
  position: absolute;
  bottom: 20px;
  width: 100%;
  text-align: center;
  font-size: 1.2rem;
  color: #feb47b;
}

/* Стили для main content */
  main {
  margin-left: 220px; /* Отступ от навигации */
  padding: 20px;
  flex: 1; /* Основной контент растягивается на оставшееся пространство */
  }
  .content-main {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff; /* Белый фон для блока с контентом */
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Тень для блока */
  }
  
  h2 {
    font-size: 2rem;
    font-weight: 600;
    text-align: left;
    color: #4a90e2; /* Цвет для заголовка */
    margin-bottom: 20px;
  }
  
  .intro {
    background-image: url('components/background123.jpg'); /* Укажите путь к вашему изображению */
    background-size: cover; /* Чтобы изображение растягивалось на весь фон */
    background-position: center center; /* Центрирует изображение */
    background-repeat: no-repeat; /* Запрещаем повторение изображения */
    text-align: center;
    color: #888; /* Легкий серый для подзаголовка */
    margin-bottom: 30px;
    background-color: #333;
    color: white;
    padding: 10px 0;
    font-size: 1.7rem;
    text-align: center;
  }
  
  .text-main h3 {
    font-size: 1.2rem;
    line-height: 1.8;
    text-align: justify; /* Выравнивание текста по ширине */
    color: #555; /* Темно-серый цвет для текста */
    padding: 0 20px;
    margin-bottom: 30px;
  }
  
  .text-main h3 br {
    margin-bottom: 20px; /* Добавление отступов после разрывов строки */
  }

section {
  margin-bottom: 40px;
}

section h2 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 20px;
}

section p {
  font-size: 1.2rem;
  color: #555;
}

/* Стили для footer */
footer {
  background-color: #333;
  color: white;
  padding: 10px 0;
  font-size: 0.9rem;
  text-align: center;
}

footer p {
  margin: 0;
}

/* Адаптивные стили для мобильных устройств */
@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
    text-align: center;
  }

  .nav {
    position: static;
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }

  .nav ul {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }

  .nav ul li a {
    padding: 10px 20px;
  }

  main {
    margin-left: 0;
  }

  .company-name {
    display: none;
  }
}







